.app-container {
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.game-container {
    width: 90%;

    display: flex;
    justify-content: space-between;
}

.grid-container {
    display: flex;
    width: auto;
    height: auto;

}

.square-column {
    display: flex;
    flex-direction: column;
}

.square {
    border: 1px solid black;
    width: 40px;
    height: 40px;
    display: block;

    background-color: #8d9191;
}

.bomb {
    background-color: #ec7d58;
}

.open {
    background-color: #cbc7c7;
}

.game-win {
    background-color: #bcff85;
}

.square:disabled {
    color: black;
}


